@font-face {
  font-family: "font-1";
  src: url(../assets/fonts/JosefinSans-Medium.ttf);
}

$font-1: "font-1", sans-serif;

$color-1: #62dcfb;
$color-2: #0c0042;
$color-3: #62dcfb;
$white: rgb(255, 255, 255);

@mixin verticalCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background: $white;
  font-family: $font-1;
  background-color: black;
  
}
li {
  list-style-type: none;
}
#lorem{
  display:flex ;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: 50px;
  color: white;
}
#lorem h1{
  margin-bottom: 50px;
}
